import React from 'react';
function Todo({setTodos,todos,todo,text}){
    const deleteTodo=()=>{
        setTodos(todos.filter(el=>el.id !== todo.id))
    }
    const completeTodo=()=>{
        setTodos(todos.map(item=>{
            if(item.id===todo.id){
                return{
                    ...item,completed:!item.completed
                };
            };
            return item;
        }))
    }
    return(
        <div className="to-do">
            <li  className={todo.completed ? 'completed' : " "}>
                {text}</li>
                <div className="buttons">
                    <button onClick={completeTodo} className={todo.completed ? 'completes' : "to-do-toggle"}>Complete</button>
                    <button onClick={completeTodo} className={todo.completed ? 'to-do-toggle' : "completes"}>Completed</button>
                    <button onClick={deleteTodo}>Delete</button>
                </div>
        </div>
    )
};
export default Todo;