import React from 'react';

function Form(props){
    
    const getInput=(e)=>{
        props.setInput(e.target.value)
    }
    const submitTodo=(e)=>{
        e.preventDefault();
        props.setTodos([
            ...props.todos, {text: props.input, completed:false, id:Math.random()*1000}
        ])
        props.setInput("");
    }
    const getStatus=(e)=>{
        props.setStatus(e.target.value)
    }
    return(
        <div>
            <div className="input_box">
                <input type="text" name="To-Do-List" value={props.input} placeholder="To-Do-List" onChange={getInput}/>
                <button type="submit" onClick={submitTodo}>Submit</button>
            </div>
            <div className="select">
                <select onChange={getStatus}>
                    <option value="all">All</option>
                    <option value="completed">Completed</option>
                    <option value="uncompleted">Uncompleted</option>
                </select>
            </div>
        </div>
    )
};
export default Form;