import React from 'react';
import Todo from './Todo';

function Home(props){
    return(
      <div>
          <ul>
              {
                  (props.filterStatus).map((todo)=>(
                      <Todo text={todo.text} key={todo.id} todos={props.todos} todo={todo}  setTodos={props.setTodos}/>
    ))
              }
          </ul>
      </div>  
    );
};
export default Home;