import React,{useState,useEffect} from 'react';
import './App.css';
import Form from './Form';
import Home from './Home';

function App() {

  const [input,setInput]=useState("");
  const [todos,setTodos]=useState([]);
  const [status,setStatus]=useState('all');
  const [filterStatus,setFilterStatus]=useState([]);

  useEffect(()=>{
    getLocalTodos();
  },[])

  useEffect(()=>{
    getFilteredStatus();
    saveLocalTodos();
  },[todos,status])


  const getFilteredStatus=()=>{
    switch(status){
        case "completed":
            setFilterStatus(todos.filter(todo=>todo.completed===true));
            break;
        case "uncompleted":
            setFilterStatus(todos.filter(todo=>todo.completed===false));
            break;
        default:
            setFilterStatus(todos);
            break;
    }
}
const saveLocalTodos=()=>{
  localStorage.setItem("todos",JSON.stringify(todos));
}
const getLocalTodos=()=>{
  if(localStorage.getItem("todos")===null){
    localStorage.setItem("todos",JSON.stringify([]));
  }
  else{
    let todoLocal=JSON.parse(localStorage.getItem("todos"))
    setTodos(todoLocal)
  }
}

  return (
    <div className="App">
      <h2>To Do List</h2>
      <Form setInput={setInput} 
      input={input} 
      todos={todos} 
      setTodos={setTodos}
      setStatus={setStatus} 
      status={status}/>

      <Home todos={todos} filterStatus={filterStatus} setTodos={setTodos}/>
    </div>
  );
}

export default App;
